@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button-show-more {
  background-color: #f2f2f2;
  border: none;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  color: #444444;
}

.button-program {
  background-color: #68b738;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  text-decoration: none;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
}

.button-order {
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  color: #222222;
  background-color: #68b738;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  text-decoration: none;
}

.page {
  height: 100%;
  background-color: #ffffff;
  font-family: "Oswald", sans-serif;
  color: #000000;
}

.page__body {
  margin: 0;
}

.main-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .main-header {
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    width: 1220px;
  }
}
.wraper-logo {
  display: flex;
  align-items: center;
}

@media (min-width: 768px) {
  .main-nav {
    width: 440px;
    display: flex;
    align-items: center;
  }

  .main-nav__toggle {
    display: none;
  }
}
.site-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.site-list__link {
  text-decoration: none;
  color: #000000;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
}

@media (min-width: 768px) {
  .site-list {
    display: flex;
  }

  .site-list__item {
    margin-right: 30px;
  }

  .site-list__link {
    text-decoration: none;
    color: #000000;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (min-width: 1440px) {
  .site-list {
    background-color: rgba(104, 183, 56, 0.85);
  }

  .site-list__link {
    text-decoration: none;
    color: #ffffff;
    font-size: 20px;
    line-height: 29.64px;
    font-weight: 400;
  }
}
.main-container__title-index {
  font-size: 60px;
  line-height: 60px;
  font-weight: 400;
}

.main-container__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
}

.main-container__description-index {
  font-size: 20px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
}

.main-container__description {
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

.cat-wraper {
  background-color: rgba(105, 183, 56, 0.85);
  background-image: url("../img/background-cat.jpg");
  background-repeat: no-repeat;
  color: #ffffff;
}

.program__wrapper {
  width: 280px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .program__wrapper {
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .program__wrapper {
    width: 1220px;
  }
}
.program-list {
  list-style-type: none;
  margin: 20px 0 0 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

@media (min-width: 768px) {
  .program-list {
    margin: 10px 0 0 0;
  }
}
@media (min-width: 1440px) {
  .program-list {
    margin: 82px 0 0 0;
  }
}
.program-item {
  width: 280px;
  background-color: #f2f2f2;
}

.program-item:not(:first-child) {
  margin: 20px 0 0 0;
}

.program-item__title {
  margin: 0;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

.program-item__description {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-family: "Arial", sans-serif;
  color: #444444;
  margin: 0;
}

.program-item__button {
  color: #000000;
  text-decoration: none;
  font-size: 14px;
  line-height: 18px;
}

@media (min-width: 768px) {
  .program-item {
    width: 708px;
  }

  .program-item:not(:first-child) {
    margin-top: 30px;
  }

  .program-item__title {
    font-size: 36px;
    line-height: 36px;
  }

  .program-item__description {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    font-family: "Arial", sans-serif;
    color: #000000;
  }
}
@media (min-width: 1440px) {
  .program-item {
    width: 570px;
    margin: 0;
  }

  .program-item:not(:first-child) {
    margin: 0;
  }

  .program-item:nth-child(odd) {
    margin-right: 80px;
  }
}
.section-title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}

@media (min-width: 768px) {
  .section-title {
    font-size: 60px;
    line-height: 60px;
  }
}
.process {
  margin: 20px 0 0 0;
}

.process__wrapper {
  width: 280px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .process {
    margin: 45px 0 0 0;
  }

  .process__wrapper {
    width: 633px;
    margin-left: 30px;
  }
}
@media (min-width: 1440px) {
  .process {
    margin: 71px 0 0 0;
  }

  .process__wrapper {
    width: 1220px;
    margin: 0 auto;
  }
}
.process-list {
  display: grid;
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
}

@media (min-width: 768px) {
  .process-list {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
@media (min-width: 1440px) {
  .process-list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
  }
}
.process-item__description {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-family: "Arial", sans-serif;
  color: #444444;
  margin: 26px 0 0 0;
}

@media (min-width: 768px) {
  .process-item__description {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    font-family: "Arial", sans-serif;
    color: #000000;
    margin: 32px 0 0 0;
  }
}
@media (min-width: 1440px) {
  .process-item__description {
    margin: 32px 0 0 0;
  }
}
.range-section {
  margin: 20px 0 0 0;
}

.range__wrapper {
  width: 280px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .range-section {
    margin: 45px 0 0 0;
  }

  .range__wrapper {
    width: 708px;
    margin-left: 30px;
  }
}
@media (min-width: 1440px) {
  .range-section {
    margin: 71px 0 0 0;
  }

  .range__wrapper {
    width: 1220px;
    margin: 0 auto;
  }
}
.example-form {
  background-color: #f2f2f2;
}

@media (min-width: 768px) {
  .example-form {
    background-color: #ffffff;
  }
}
.example {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0;
  padding: 0;
  border: none;
}

.example__description {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-family: "Arial", sans-serif;
  color: #444444;
  margin: 0;
  grid-column: 1/3;
}

.example__wrapper-weight {
  display: flex;
  flex-direction: column;
  grid-column: 1/2;
}

.example__wrapper-time {
  display: flex;
  flex-direction: column;
  grid-column: 2/3;
}

.example__result {
  margin: 0 auto;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  grid-column: 1/3;
}

@media (min-width: 768px) {
  .example {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .example__description {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    font-family: "Arial", sans-serif;
    color: #000000;
    grid-column: 1/4;
  }

  .example__result {
    margin: 0 auto;
    font-family: "Oswald", sans-serif;
    font-size: 20px;
    line-height: 20px;
    font-weight: 400;
    text-transform: uppercase;
    grid-column: 3/4;
  }
}
@media (min-width: 1440px) {
  .example__description {
    grid-column: 1/3;
  }

  .example__result {
    grid-column: 1/3;
  }
}
.example-filter-label {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
  color: #444444;
}

.range {
  grid-column: 1/3;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .range {
    grid-column: 1/4;
  }
}
@media (min-width: 1440px) {
  .range {
    grid-column: 3/4;
  }
}
.range-scale {
